:root {
  --color-gray-80: #33455a;
  --color-gray-60: #65778c;
  --color-gray-40: #a0a9b1;
  --color-gray-20: #e1e3e5;
  --color-gray-10: #f4f6f8;
  --color-brand-black: #001834;
  --color-brand-blue: #0075ff;
  --color-brand-orange: #ff8a00;
  --color-brand-orange-hover: #ffa412;
  --color-brand-white: #fff;
  --color-other-dark-blue: #006bf5;
  --color-other-icon: #c2a179;
  --color-other-background: #fffaf4;
  --color-expanded-success: #23d35e;
  --color-expanded-info: #23a9d3;
  --color-expanded-warning: #ea4;
  --color-expanded-error: #e44;
  --main-text-font: "Noto Sans", sans-serif;
  --secondary-text-font: "Century Gothic", sans-serif;
  --header-index: 5;
  --mobile-menu-index: 4;
  --main-index: 2;
  --secondary-index: 1;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--color-brand-black);
  background-color: var(--color-brand-white);
  font-family: var(--main-text-font);
}

.container {
  width: 100%;
